<template>
	<div id="creatTable">
		<div class="box">
			<!---->
			<div class="header">
				<p>
					<span class="black" @click="goBackToTable">   </span>
					<span class="blue">></span>
					<span class="blue" v-if="$route.query.id==null"></span>
					<span class="blue" v-if="$route.query.id!=null"></span>
				</p>

			</div>

			<!---->
			<div class="text">
				
			</div>

			<!---->
			<div class="set">
				<div class="form">
					<el-form :model="form" :rules="rules" label-width="135px" ref='Form'>
						<el-form-item label="">
							<span>{{form.TABLE_NO}}</span>
						</el-form-item>
						<el-form-item label="" class="qritem">
							<span class="QR"><img :src="qrurl" class="img"/></span>
						</el-form-item>
						<el-form-item label="" prop="SEAT_NAME">
							<el-input v-model="form.SEAT_NAME" @change="changeIsDisable('Form')"></el-input>
						</el-form-item>
						<el-form-item label="" prop="SEAT_SIZE">
							<el-input v-model.number="form.SEAT_SIZE" @change="changeIsDisable('Form')"></el-input>
						</el-form-item>
						<div v-for="(item,index) in nameArray" class="select">
							<el-form-item :label="item.label" :prop="item.id">
								<el-radio-group v-model="form[item.id]" fill="#4877E7" @change="handEdit(item)">
									<el-radio-button :label="$data[item.id]" v-if="$data[item.id]"></el-radio-button>
									<el-radio-button label="" class="add" ref="item.id"></el-radio-button>
								</el-radio-group>
							</el-form-item>
						</div>
						<el-form-item label="">
							<el-radio-group v-model="form.PRICE">
								<el-radio label="0"></el-radio>
								<el-radio label="1"></el-radio>
								<el-radio label="2"></el-radio>
								<el-radio label="3"></el-radio>
							</el-radio-group>
						</el-form-item>
						<el-form-item label="" prop="inputPrice" v-if="form.PRICE=='1'">
							<el-input v-model.number="form.inputPrice" class="price">
								<el-button slot="append" size="small"> / </el-button>
							</el-input>
						</el-form-item>
						<el-form-item label="" prop="inputPrice" v-if="form.PRICE=='2'">
							<el-input v-model.number="form.inputPrice" class="price">
								<el-button slot="append" size="small"> / </el-button>
							</el-input>
						</el-form-item>
						<el-form-item label="" prop="inputPrice" v-if="form.PRICE=='3'">
							<el-input v-model.number="form.inputPrice" class="price">
								<el-button slot="append" size="small">% / </el-button>
							</el-input>
						</el-form-item>

						<!--/*************/-->
						<!--<el-form-item label="">
							<el-input type="number" v-model.number="price" class="price1">
								<el-button slot="append" size="small"></el-button>
							</el-input>
						</el-form-item>-->
					</el-form>

					<!---->

					<div class="btnGroup">
						<span @click="onSave('Form')" class="spanBtn save_btn" :plain="true" v-if="isDisabled==false"></span>
						<span class="spanBtn save_btn disabledBtn" :plain="true" v-if="isDisabled==true"></span>
						<span class="spanBtn add_btn" v-if="$route.query.id==null" @click="saveAdd('Form')"> </span>
						<span class="spanBtn cancel_btn" @click="onConcel"></span >
			  </div>
			
		</div>
	</div>
	<!---->
	<select-edit v-if="showSelectEdit"  @my-add="show" :name="selectName" :history="historyText" @my-cancle="show2" :hisTaOb="historyTag"></select-edit>
	
		</div> 
	</div>
</template>
<script>
	import { apiMethods } from'../../api';
	import selectEdit from "./selectEdit";
	import { Public } from '@/config/public';
	import { ls } from "@/config/pageStore";
	import { seatPriceType } from "@/config/cfg";
	import { commonMethods } from '../index'
	export default{
		data() {
			var validateSeatName = (rule, val, callback) => {
		      if (!val) {
		        callback(new Error(""));
		      }else if(this.$route.query.id==null&&this.seatNameArr.indexOf(val)!==-1){
		      	this.$slnotify({
		            message: ""
		          });
		      	callback(new Error(""));
		      }else  {
			        callback();
		      }
		    };
			 var validateInput = (rule, val, callback) => {
		      if (!val) {
		        callback(new Error(""));
		      }else{
		      	 if(!Number.isInteger(val)){
		      	callback(new Error(""));
		      } else {
			      	if (val<=0) {
			        callback(new Error(""));
			      } else {
			        callback();
			      }
		      }
		      }
		     
		    };
		      var checkPrice = (rule, value, callback) => {
//		      	value = this.inputVal
		      	var reg =/^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
		        if (value === '' ||value <=0) {
		          callback(new Error(''));
		        }else if(reg.test(value)==false){
				   callback(new Error(""));
		       } else {
		          callback();
		        }
		      };
			return{
				idcha:null,
				historyText:"",//
				historyTag:{},//
				selectName:"",//
				name:{},//idlabel
				showTag:false,//
				inputValue:{},//
				seatNameArr:[],//
				qrurl:"",
				isDisabled:true,
				id:null,//;
		        price:0,//
		        TABLE_TYPE:"",//
		        SEAT_TYPE:"",//
		        SEAT_REGION:"",//
		        idInfo:null,//
		        form: {			//
		          TABLE_NO: '',//
		          SEAT_NAME: '',//
		          SEAT_TYPE: '',//
		          SEAT_SIZE: 1,//
		          SEAT_REGION: "",//
		          TABLE_TYPE:"",//
		          PRICE:"", //
		          inputPrice:""
		        },
		        nameArray:[//
		        	{label: '', id: 'SEAT_REGION'},
                    {label: '', id: 'SEAT_TYPE'},
                    {label: '', id: 'TABLE_TYPE'},
		        ],
		        rules: {	//
		          SEAT_NAME: [
		            { required: true,validator: validateSeatName ,trigger: "click" }
		          ],
		          SEAT_SIZE: [
					{required: true,validator: validateInput, trigger: "click" }
		          ],
		          SEAT_REGION: [
		          { required: true, message: '' ,trigger: "click" }
		          ],
		          SEAT_TYPE: [
		          {required: true, message: '' ,trigger: "click"}
		          ],
		          inputPrice:[
		          {required: true,validator:checkPrice, trigger: "click"}
		          ]
		        },
		      
			}
		},
		components:{selectEdit},
		created() {
			
			if(ls.getItem("hisTaOb")!==null){
				this.historyTag=ls.getItem("hisTaOb")
			}
			this.initEditeData();
			
		},
		computed:{
			//
			showSelectEdit() {
				return this.showTag
			},
			inputVal () {
				if(this.form.PRICE==seatPriceType.PERCENTAGE&&this.inputValue[this.form.PRICE]>=100) {
					this.form.inputPrice=0
				}else{
					 this.form.inputPrice=this.inputValue[this.form.PRICE]
				}
				return this.form.inputPrice
			}
		},
		methods:{
//			
			initEditeData() {
				//
				if(!this.$route.query.id){
					apiMethods.getId({
					genid:1,
					type:"seat"
					},(res)=>{
						this.form.TABLE_NO = res.data.id;
						this.form.PRICE= "0"
						this.getQR();
					})
				}else{
					this.id=this.$route.query.id;
					apiMethods.getTableEditor({
					get_seat_list:1
					},(resp)=>{
						for(var i in resp.data.seatlist){
			        	if(resp.data.seatlist[i].seat_id===this.id){
			        		this.idInfo = resp.data.seatlist[i]
			        	}
			     	}
					this.TABLE_TYPE=this.form.TABLE_TYPE=this.idInfo.seat_shape||"";
					this.form.TABLE_NO=this.idInfo.seat_id;
					this.form.SEAT_NAME=this.idInfo.seat_name||"";
					this.form.SEAT_SIZE=this.idInfo.seat_size||"";
					this.SEAT_REGION=this.form.SEAT_REGION=this.idInfo.seat_region||"";
					this.SEAT_TYPE=this.form.SEAT_TYPE=this.idInfo.seat_type||"";
					this.form.PRICE=JSON.stringify(this.idInfo.price_type);
					this.form.inputPrice = this.idInfo.price;
					/*
					 
					 * if(this.form.PRICE==seatPriceType.PERCENTAGE){
						this.inputValue[this.form.PRICE]=this.idInfo.price;
					}else{
						this.inputValue[this.form.PRICE]=this.idInfo.price==0||this.idInfo.price==""?"":Number(this.idInfo.price).toFixed(2)
					}
					
					 * 
					 * */
					
//					this.price=this.idInfo.consume_min||0;
						this.getQR();
					});
					
				}	
				apiMethods.getTableData({
					get_seat_list: 1
				}, (resp) => {
					if(resp.ret==0) {
						resp.data.seatlist.forEach((item)=>{
							if(this.seatNameArr.indexOf(item.seat_name)==-1) {
								this.seatNameArr.push(item.seat_name)
							}
							
						})
					}
				})
				
			},
			//
			getQR() {
				let shopiin = Public.getShopid();
				this.qrurl = base_url +'/img_get.php?get_seat_qrcode=1&shop_id='+shopiin+'&seat_id=' + this.form.TABLE_NO;
			},
			//
		    submitForm(formName,fnAfterSave) {
		      this.$refs[formName].validate(valid => {
		      
				 if (valid) {
				 	if(this.form.PRICE==0) {
				 		this.form.inputPrice=0
				 	}
		         apiMethods.changeTableData({
	      			seat_save:1,
		        	seat_id:this.form.TABLE_NO, //id
		        	price:this.form.inputPrice,//
		        	seat_name:this.form.SEAT_NAME,//
		        	seat_region:this.form.SEAT_REGION,//
		        	seat_shape:this.form.TABLE_TYPE,//
		        	seat_size:this.form.SEAT_SIZE,//
		        	seat_type:this.form.SEAT_TYPE,//
//		        	consume_min:this.price,//
		        	price_type:this.form.PRICE//
	      		 },fnAfterSave)
		        } else {
		          console.log("error submit!!");
		          return false;
		        }
		        
		     });
		    },
			//
			Refresh(formName) {
				this.$slnotify({
		            message: "",
		             duration:1000
		          });
		          
		          var timer;
		           clearTimeout(timer)
		         timer= setTimeout(()=>{
		         	this.$data["SEAT_REGION"] =""
		         	this.$data["TABLE_TYPE"] = ""
		         	this.$data["SEAT_TYPE"] = ""
		         	apiMethods.getId({
					genid:1,
					type:"seat"
					},(res)=>{
						this.form.TABLE_NO = res.data.id;
						this.form.PRICE= "0"
						this.getQR();
					})
		         	
		         	this.$refs[formName].resetFields();
		          },500)
					
			},
			//
			goTablePage() {
				this.$slnotify({
		            message: "",
		            duration:1000
		          });
		            var timer;
		        clearTimeout(timer)
		         timer= setTimeout(()=>{
		          		this.$router.go(-1);
		          },1000)
		           
			},
 			//
			onSave(formName) {
					this.submitForm(formName,this.goTablePage);
			},
			//
			onConcel() {
				this.initEditeData()
				this.$router.go(-1)
			},
			//
			goBackToTable() {
				this.initEditeData()
				this.$router.go(-1)
			},
			//
			handEdit(item) {
				this.isCanClick('Form')
				if(this.form[item.id]==""){
					this.showTag = true;
					this.selectName = item;
						this.historyText = this.$data[item.id];
						if(ls.getItem("hisTaOb")==undefined){
							this.historyTag[item.id]=this.historyText;
						}else{
							this.historyTag=ls.getItem("hisTaOb")
						}
				}
			},
			//
			changeIsDisable(formName) {
				this.isCanClick(formName)
			},
			//
			saveAdd(formName) {
				
				this.submitForm(formName,this.Refresh(formName))
			},
			//
			show(data) {
					this.showTag=false
					if(data[0]==""){
						this.form[data[1]]=this.$data[data[1]]
					}else{
						this.$data[data[1]]=data[0];
						this.form[data[1]]=this.$data[data[1]];
					}
						this.historyTag[data[1]]=data[2]
						this.name[data[1]]=data[2]
						if(this.historyText!=="") {
							if(this.historyTag[data[1]].indexOf(this.historyText)==-1) {
								this.$data[data[1]] = ""
								this.historyText = ""
							}
						}
						
						ls.setItem("hisTaOb",this.historyTag)
			},
			//
			show2(data) {
					this.showTag=false;	
					this.form[data[0]]=this.$data[data[0]]
					this.historyTag[data[0]]=data[1];
					ls.setItem("hisTaOb",this.historyTag)
			},
			//
			isCanClick(formName) {
				  this.$refs[formName].validate(valid => {
				 if (valid) {
				 	this.isDisabled = false
				 	
				 }
				 })
				
			}
		},
	}
</script>

<style lang="scss" scoped>
@import 'src/styles/mixin.scss';
	.box {
		height: 100%;
	}
	
	.header {
		height: 16px;
		font-family: MicrosoftYaHei;
		font-size: 12px;
		color: #333333;
		letter-spacing: 0.43px;
		margin-bottom: 14px;
	}
	
	.header span {
		margin: 2px;
	}
	
	.blue {
		color: #4877E7;
		cursor: pointer;
	}
	
	.black {
		cursor: pointer;
	}
	
	.text {
		height: 40px;
		font-size: 14px;
		color: #666666;
		letter-spacing: 0.5px;
		background: #F6F8FC;
		padding: 14px;
	}
	
	.set {
		background: #FFFFFF;
		font-size: 14px;
		color: #666666;
		position: relative;
	}
	
	.seat_no {
		padding: 48px 168px 27px;
		font-size: 14px;
		color: #000000;
		letter-spacing: 0.5px;
	}
	
	.qr {
		padding-left: 175px;
		margin-bottom: 20px;
	}
	.QR img{
		width: 100px;
		height: 100px;
	}
	.set span {
		margin-right: 14px;
	}
	
	.qrPicture {
		position: absolute;
		top: 98px;
		left: 240px;
		width: 100px;
		height: 100px;
	}
	
	.form {
		margin: 0px 104px 14px;
		padding-top: 50px;
	}
	
	.el-input {
		width: 280px;
		height: 34px;
	}
	
	.el-form-item {
		margin-bottom: 24px;
	}
	
	.el-input.price1,
	.el-input.price {
		width: 190px;
		height: 30px;
	}
	
	.btnGroup {
		width: 100%;
		margin-left: 15%;
		margin-top: 114px;
	}
	
	.btnGroup .spanBtn {
		color: white;
		width: 160px;
		height: 40px;
		text-align: center;
		line-height: 40px;
		font-size: 14px;
		margin-right: 10px;
		margin-bottom: 50px;
		display: inline-block;
		border-radius: 5px;
		cursor: pointer;
	}
	
	.btnGroup .save_btn {
		margin-left: 20%;
		color: #4877E7;
		border: 1px solid #4877E7;
		cursor: pointer;
	}
	
	.btnGroup .disabledBtn {
		border: 1px solid #D8D8D8;
		cursor: pointer;
		color: #FFFFFF;
		background: #D8D8D8;
	}
	
	.btnGroup .add_btn {
		margin-right: 10px;
		color: #4877E7;
		border: 1px solid #4877E7;
		cursor: pointer;
	}
	
	.btnGroup .cancel_btn {
		color: #4877E7;
		border: 1px solid #4877E7;
		cursor: pointer;
	}
	
	.tagGroupName {
		display: inline-block;
	}
	
	.tag {
		width: 80px;
		color: #4877E7;
		height: 30px;
		font-size: 13px;
		text-align: center;
		vertical-align: middle;
		margin: 0 10px 10px;
		border-radius: 5px;
		border: 1px solid #4877E7;
		display: inline-block;
		cursor: pointer;
	}
	
	.tagGroup {
		width: 400px;
		list-style: none;
	}
	
	.tag.selected {
		color: green;
		border: 1px solid green;
	}
	
	.earror {
		border: 1px solid #ff4949;
	}
</style>
<style lang="scss" >
@import 'src/styles/mixin.scss';
	#creatTable {
		.el-radio-button .el-radio-button__inner {
			width: 80px;
			height: 33px;
			border: none;
			margin-right: 10px;
			border: 1px solid #4877E7;
			cursor: pointer;
		}
		.el-input__inner {
			border-radius: 0;
		}
		.el-radio-button:first-child .el-radio-button__inner,
		.el-radio-button:last-child .el-radio-button__inner {
			border-radius: 4px;
			cursor: pointer;
		}
		.el-input-group--append .el-input__inner {
			width: 150px;
		}
		.el-input-group__append .el-button {
			width: 55px;
		}
		.is-error .el-input__inner {
			border: 1px solid #E7487E;
		}
		.is-error .el-radio-button .el-radio-button__inner {
			border: 1px solid #E7487E;
		}
		.el-form-item__error {
			position: absolute;
			top: 30%;
			left: 300px;
			font-size: 12px;
			color: #E7487E;
		}
		.el-form-item.qritem {
			margin-top: 5px;
			margin-bottom: 10px;
		}
		.disabledBtn.el-button {
			border: 1px solid #D8D8D8;
			cursor: pointer;
			color: #FFFFFF;
			background: #D8D8D8;
		}
	}
</style>


